import React, { useEffect, useState } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { Worker, Viewer } from '@react-pdf-viewer/core';
import '@react-pdf-viewer/core/lib/styles/index.css';
import dataPage from './dataPage';
import { pdfjs } from 'react-pdf'; 

const PdfDownload = () => {
  const { subjectId, semester } = useParams();
  const subject = dataPage.find((subject) => subject.id === parseInt(subjectId));
  const navigate = useNavigate();
  const [pdfPath, setPdfPath] = useState('');

  useEffect(() => {
    if (!subject || !subject.Semester[semester]) {
      navigate('/');
    } else {
      const path = process.env.PUBLIC_URL + subject.Semester[semester].pdfPath;
      setPdfPath(path);
    }
  }, [subject, semester, navigate]);

  if (!subject || !subject.Semester[semester]) {
    return null;
  }

  const downloadPdf = () => {
    const link = document.createElement('a');
    link.href = pdfPath;
    link.download = `Subject_${subjectId}_${semester}.pdf`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <div id='pdfView'>
      
      <h1 id='h1'>PDF for</h1>
      <h2>{subject.Subject}</h2>
      <h2 id='semester-h2'>- {semester}</h2>
      <div id='pdf-T'>
      <button onClick={downloadPdf} id='download'>
         Download
      </button>
      <Link to={`/subject/${subjectId}`} id='back_to'>
        Back to Semesters
      </Link>
      </div>

      <div className="pdf-viewer-container">
        <Worker workerUrl={`https://unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`}>
          <Viewer fileUrl={pdfPath} />
        </Worker>
      </div>
    </div>
  );
};

export default PdfDownload;










